<template>
  <div class="container">
    <transition-group class="wrap" name="fade">
      <div class="person" v-for="(person, index) in persons" v-bind:key="index">

        <card>

          <img slot="image" :src="person.picture.large">
          <p slot="name">
            <span>{{ person.name.first }} </span>
            <span>{{ person.name.last }}</span>
          </p>
          <ul class="info" slot="info">
            <li>
              <strong>Age:</strong> {{ person.dob.age }}
            </li>
            <li>
              <strong>Email:</strong> {{ person.email }}
            </li>
            <li>
              <strong>Location:</strong> {{ person.location.city }},
              {{ person.location.state }}
            </li>
          </ul>


        </card>
        <!-- <div class="left">
          <img :src="person.picture.large">
        </div>
        <div class="right">
          <p>{{ person.name.first }} {{ person.name.last }}</p>
          <ul>
            <li>
              <strong>Birthday:</strong> {{ person.dob }}
            </li>
            <li class="text-capitalize">
              <strong>Location:</strong> {{ person.location.city }},
              {{ person.location.state }}
            </li>
          </ul>
        </div> -->
      </div>
  </transition-group>

  </div>
</template>


<script>
import Card from "@/components/Card";

export default {
  name: "Scroll",
  components: {
    Card
  },
  data() {
    return {
      persons: []
    };
  },
  methods: {
    getInitialUsers() {
      for (var i = 0; i < 8; i++) {
        this.axios.get(`https://randomuser.me/api/`).then(response => {
          this.persons.push(response.data.results[0]);
        });
      }
    },
    scroll() {
      window.onscroll = () => {
        let bottomOfWindow =
          document.documentElement.scrollTop + window.innerHeight ===
          document.documentElement.offsetHeight;

        if (bottomOfWindow) {
          for (var i = 0; i < 4; i++) {
            this.axios.get(`https://randomuser.me/api/`).then(response => {
              this.persons.push(response.data.results[0]);
            });
          }
        }
      };
    }
  },
  beforeMount() {
    this.getInitialUsers();
  },
  mounted() {
    //do something after mounting vue instance
    this.scroll();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import url("https://fonts.googleapis.com/css?family=Playfair+Display:400,700");

.container {
  height: 100%;
  padding: 40px;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.wrap {
  width: 100%;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.person {
  width: 400px;
  margin: 0 auto 15px auto;
  margin: 20px 10px;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}

ul {
  list-style-type: none;
  padding: 0;
}

a {
  color: #42b983;
}
</style>



